<template>
  <li class="article-item">
      <a :href="titleHref"><h4 class="article-title">{{title}}</h4></a>
      <p class="article-abstract">{{abstract}}</p>
      <span class="article-date">{{date}}</span>
  </li>
</template>

<script>
export default {
    props:{
        title:{
            type:String,
            required:true
        },
        abstract:{
            type:String,
            required:true
        },
        titleHref:{
            type:String,
            required:true
        },
        date:{
            type:String,
            required:true
        }
    }
}
</script>

<style>
.article-item {
  position: relative;
  width: 100%;
  margin: 0 0 15px;
  padding: 10px 20px;
  background: #ffffff;
  border: 1px solid #f0f0f0;
  border-radius: 20px;
  word-wrap: break-word;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s;
}
.article-item:hover{
    box-shadow: 0 2px 16px rgba(0, 0, 0, 0.2);
}
.article-item a{
    text-decoration: none;
    color: black;
}
.article-item a:hover{
    text-decoration: underline;
}
.article-item .article-abstract{
    font-size: 14px;
    color: rgba(0,0,0,0.75);
    line-height: 1.5;
}
.article-item .article-date{
    font-size: 10px;
    color: rgba(0,0,0,0.5);
}
</style>
